@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{z-index:100;position:fixed;top:0;width:100%;color:#fff;height:80px;display:flex;align-items:center;justify-content:space-between;padding:0 5vw;background:#212121;box-shadow:0 0 10px 3px #00000020}nav .logo svg{height:50px;width:50px;transition:.2s ease-in}nav .logo svg:hover{border:2px solid #00e5ff;border-radius:50%}nav .logo svg:hover #hex{opacity:0;transition:.2s ease-in}nav .logo svg #hex{opacity:1;transition:.2s ease-in}nav a{text-decoration:none;font-weight:600}nav .nav-links{display:flex;align-items:center;justify-content:space-between;counter-reset:link}nav .nav-links .nav-link{margin:0 20px;position:relative;color:#ccc;transition:.1s ease-in}nav .nav-links .nav-link:hover{color:#fff}nav .nav-links .nav-link:hover::before{color:#00e5ff}nav .nav-links .nav-link::before{counter-increment:link;content:"0" counter(link) ". ";font-family:'Fira Code', monospace;text-align:right;color:#00e5ff80;transition:color .2s ease-in}nav .resume-btn{color:#00e5ff;border:2px solid #00e5ff;padding:10px;transition:.1s ease-in}nav .resume-btn:hover{background:#00e5ff20}.nav-link::after{content:'';height:2px;background:#fff;position:absolute;bottom:-5px;width:0%;left:0;transition:width .2s ease-in}.nav-link:hover::after{width:100%}

.projects-section .wrapper{max-width:1000px;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

.social-media-bar {
	position: fixed;
	height: 300px;
	width: 60px;
	bottom: 0;
	left: 5vw;
	list-style-type: none;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}

.social-media-bar svg {
	width: 20px;
	height: 20px;
	fill: #fff;
	transition: .2s ease-in;
}

.social-media-bar::after {
	content: '';
	background: #fff;
	height: 90px;
	width: 1px;
}

.social-media-bar svg:hover {
	fill: #00e5ff;
	transform: translateY(-3px);
}

.contact-bar {
	/* border: thin solid white; */
	text-decoration: none;
	position: fixed;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	letter-spacing: .5ch;
	font-size: 12px;
	font-family: 'Fira Code', monospace;
	-webkit-writing-mode: vertical-rl;
	    -ms-writing-mode: tb-rl;
	        writing-mode: vertical-rl;
	bottom: 0;
	right: 5vw;
	/* margin: 20px auto; */
	transition: .2s ease-in;
}

.contact-bar::after {
	content: "";
	display: inline-block;
	bottom: 0;
	/* position: absolute; */
	width: 1px;
	height: 90px;
	background: #fff;
	margin: 0px auto;
}

.contact-bar:hover {
	color: #00e5ff;
	transform: translateY(-3px);
}

.intro-section{display:flex;align-items:center;justify-content:center;flex-direction:column}.intro-section .text-wrapper{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center}.intro-section .text-wrapper .hello{font-family:"Fira Code", monospace;color:#00e5ff;font-size:1.5em}.intro-section .text-wrapper .name{font-weight:900;font-size:4em;padding:10px 0}.intro-section .text-wrapper .occupation{font-size:2em;font-weight:600;color:#aaa}.intro-section .text-wrapper .say-hi-btn{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-top:50px;padding:20px 30px;border:2px solid #00e5ff;color:#00e5ff;text-decoration:none;font-weight:600;transition:.1s ease-in}.intro-section .text-wrapper .say-hi-btn:hover{background:#00e5ff20}

.about-section .wrapper{max-width:1000px;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex:1 1;flex-direction:row;align-items:center;justify-content:space-between}.about-section .wrapper .left{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;flex:0.6 1}.about-section .wrapper .left .desc{max-width:45ch;line-height:1.5;color:#aaa;font-size:1em;font-weight:600;word-spacing:3px}.about-section .wrapper .left .languages{padding:30px 0;display:grid;grid-template-columns:auto auto}.about-section .wrapper .left .languages li{list-style-type:none;position:relative;font-family:"Fira Code", monospace;font-weight:100;font-size:0.9em}.about-section .wrapper .left .languages li::before{content:"▹ ";position:relative;left:0px;color:#00e5ff}.about-section .wrapper .right{position:relative;display:flex;align-items:center;justify-content:center;flex:0.3 1}.about-section .wrapper .right::before{content:'';height:100%;width:100%;position:absolute;top:20px;left:20px;border:2px solid #00e5ff;box-shadow:0 0 10px 5px #00e5ff20;transition:.1s ease-in}.about-section .wrapper .right:hover::before{transform:translate(-10px, -10px);box-shadow:0 0 15px 5px #00e5ff50;transition:.1s ease-in}.about-section .wrapper .right a{position:relative;width:100%;height:100%;cursor:pointer}.about-section .wrapper .right a #profile-pic{width:100%;height:100%;object-position:center center;object-fit:cover}

.experience-section .wrapper{max-width:1000px;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;align-self:flex-start;padding-top:30%}.experience-section .wrapper .inner-wrapper{display:flex;justify-content:space-between;flex:1 1}.experience-section .wrapper .inner-wrapper .tab-slider{align-self:flex-start;flex:.3 1;position:relative;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;align-self:flex-start}.experience-section .wrapper .inner-wrapper .tab-slider::before{content:'';position:absolute;left:2px;top:0;height:100%;width:1px;background:#aaa}.experience-section .wrapper .inner-wrapper .tab-slider .tab-indicator{position:absolute;top:0;left:0;background:#00e5ff;width:5px;border-radius:5px;transition:.1s ease-in}.experience-section .wrapper .inner-wrapper .tab-slider .tab{display:flex;align-items:center;list-style-type:none;position:relative;padding:15px;cursor:pointer}.experience-section .wrapper .inner-wrapper .tab-slider .tab:hover{background:#ffffff10}.experience-section .wrapper .inner-wrapper .tab-slider .tab.active{color:#00e5ff}.experience-section .wrapper .inner-wrapper .info{align-self:flex-start;flex:.7 1;padding:10px 20px;position:relative;overflow:hidden}.experience-section .wrapper .inner-wrapper .info::before{content:'';position:absolute;width:1px;left:0;top:0;height:100%;background:#aaa}.experience-section .wrapper .inner-wrapper .info .card{position:relative;height:100%;width:100%}.experience-section .wrapper .inner-wrapper .info .card .info-title{font-weight:900;padding-bottom:20px;font-size:1.2rem;color:#00e5ff}.experience-section .wrapper .inner-wrapper .info .card .duration{padding-bottom:20px;font-size:0.9rem;font-family:'Fira Code', monospace}.experience-section .wrapper .inner-wrapper .info .card .desc-list li{list-style-type:none;color:#aaa;font-size:0.95rem;font-weight:600;padding:5px 0}.experience-section .wrapper .inner-wrapper .info .card .desc-list li .sub-list{padding-left:20px}.experience-section .wrapper .inner-wrapper .info .card .desc-list li .sub-list li{font-size:.9rem;font-weight:100;font-family:'Fira Code', monospace}.experience-section .wrapper .inner-wrapper .info .card .desc-list li::before{content:"▹ ";position:relative;left:0px;color:#00e5ff}

